<template>
	<section class="aui-flexView">
		<section class="aui-scrollView">
			<view class="aui-tab" data-ydui-tab>
				<view class="tab-panel" v-if="!noOrder">
					<block v-for="(cart,index3) in cartList" :key="index3">
						<view class="tab-item" style="border-bottom: 1px solid #ddd;">
							<a href="javascript:void(0);" class="aui-well-item aui-well-item-clear">
								<view class="aui-well-item-bd">
									<text style="font-size: 13px;">{{cart.order_id}}</text>
								</view>
							</a>
							<view class="shop_details" style="width: 100%;height: 240upx;padding: 10px 20px;background:url(../../static/timg.jpg) no-repeat 100%;" @click="cdShopDetail(cart.exchange.exchange)">
								<view class="image" style="float: left;">
									<image style="width: 200upx;height: 200upx;" :src="cart.exchange.exchange.image" mode=""></image>
								</view>
								<view style="float: right;">
									<text style="font-size: 16px;font-weight: bold;">{{cart.exchange.exchange.name}}</text><br>
									<view style="margin-top: 16upx;line-height: 24px;font-size: 24upx;color: #999;">
										<text>{{cart.exchange.exchange.explain}}</text><br>
									</view>
								</view>
							</view>
							<view class="aui-mail-button" @click="lookOrder(cart)">
								<a class="aui-df-color">查看订单</a>
							</view>
						</view>
					</block>
				</view>
				<view class="" v-else>
					<xw-empty :isShow="noOrder" text="暂无相关订单数据" textColor="#777777"></xw-empty>
				</view>
			</view>
		</section>
	</section>
</template>

<script>
	import uniRequest from 'uni-request';
	import empty from "@/components/empty";
	import xwEmpty from '@/components/xw-empty/xw-empty';
	export default {
		data() {
			return {
				scrollLeft: 0,
				isClickChange: false,
				currentTab: 0,
				cartList:[],
				noOrder:false,
			}
		},
		components:{empty,xwEmpty},
		onLoad(option) {
			uniRequest.get('/orders/exchange/list/?user='+option.oid).then(res=>{
				console.log(res.data)
				if(res.data.length < 1){
					this.noOrder = true
				}
				this.cartList = res.data.reverse()
			}).catch(error=>{
				console.log(error)
			})
		},
		mounted(){
			let a = document.getElementsByClassName('uni-page-head-hd')[0]
			a.style.display = 'none'
		},
		methods: {
			swichMenu: async function(current) { //点击其中一个选项
				if (this.currentTab == current) {
					return false;
				} else {
					this.currentTab = current;
					this.setScrollLeft(current);
				}
			},
			swiperChange: async function(e) {
				let index = e.target.current;
				this.setScrollLeft(index);
				this.currentTab = index;
			},
			setScrollLeft: async function(tabIndex) {
				let leftWidthSum = 0;
				for (var i = 0; i <= tabIndex; i++) {
					let nowElement = await this.getWidth('tabNum' + i);
					leftWidthSum = leftWidthSum + nowElement.width;
				}
				let winWidth = uni.getSystemInfoSync().windowWidth;
				this.scrollLeft = leftWidthSum > winWidth ? (leftWidthSum - winWidth) : 0
			},
			getWidth: function(id) { //得到元素的宽高
				return new Promise((res, rej) => {
					uni.createSelectorQuery().select("#" + id).fields({
						size: true,
						scrollOffset: true
					}, (data) => {
						res(data);
					}).exec();
				})
			},
			lookOrder(item){
				uni.navigateTo({
					url:'/pages/cdkey/cdkeyOrder?data='+JSON.stringify(item)
				})
			},
			cdShopDetail(item){
				uni.navigateTo({
					url:'/pages/cdkey/shopDetail?data='+JSON.stringify(item)
				})
			}
		}
	}
</script>

